#app { height: 100% }



.swiper {
  width: 100%;

  overflow: auto;
  overflow-x: hidden;

}

.swiper-slide {
  text-align: center;
  font-size: 18px;


  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.swiper-pagination-bullets.swiper-pagination-horizontal {
    top: 300px;
    left: 0;
    width: 100%;
}

.swiper-button-prev {
    top: 100px;
}



