* {
   box-sizing: border-box;
   margin: 0;
   padding: 0;
   /* border: 2px solid red */
}
.Navbar {
   background-color: rgba(144, 144, 33, 0.3);
   padding: 10px 0 10px 0;
 
   width: 100%;
}
.navbar {
   display: flex;
   width: 100%;
   justify-content: space-between;
   align-items: center;
  
}


.navbar__image {
   width: 200px;
   height: 90px;
}

.navbar__image img {
   width: 100%;
   height: 100%;
   object-fit: fill;
}

.navbar__center ul {
   list-style: none;
   display: flex;
   align-items: center;
   justify-content: center;
}

.navbar__center ul li {
   margin-right: 12px;
}

.navbar__center ul li a {
   text-decoration: none;
   font-family: Oswald;
   font-size: 18px;
   color: white
  
}

.navbar__button button {
   border: none;
   width: 200px;
   height:60px;
   clip-path: polygon(30% 0%, 70% 0%, 100% 30%, 100% 70%, 70% 100%, 30% 100%, 0% 70%, 0% 30%);
   background-color: #3F67AA;
   color: white;
   font-size: 15px;
   transition: 0.3s all;
}
.navbar__button button:active {
   transform: scale(0.78);
   transition: 0.3s all;
}




@media(max-width: 1200px) {
   .navbar__image {
      width: 160px;
      height: 70px;
   }
}







@media(max-width: 995px) {
  
   .navbar__image {
      display: none;
   }
   .navbar__center ul li {
      margin-right: 8px;
   }
   .navbar__center ul li a {
      text-decoration: none;
      font-family: Oswald;
      font-size: 15px;
      color: white
     
   }
   .navbar__button button {
     
      width: 160px;
      height: 40px;
    
   }
}

@media(max-width: 995px) {
   .navbar {
      justify-content: center;
   }
   .navbar__button button {
     width: 100px;
     height: 30px;
     font-size: 10px;
   }
}


